.button {
  padding: 0.5rem 1rem;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 5rem;
  margin: 0 0.25rem;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.button:hover {
